import React from "react";
import ReactDOM from "react-dom";
import { ChakraProvider, SimpleGrid, Container } from "@chakra-ui/react";
import Card from "./Card";

function App() {
  const dataList = [
    {
      id: "1",
      product: "Susana Sánchez Carballo",
      summary: "This is a summary, can be any length",
      longLine: "Artista visual, feminista, investigadora, gestora cultural y docente universitaria."
    },
    {
      id: "2",
      product: "Sussy VArgas Alvarado",
      summary:
        "Another summary, make sure that this is very responsivesfafsdfsdfsdfsdfsfsfsdf",
      longLine: "Licenciada en Artes Plásticas con énfasis en Pintura de la Universidad de Costa Rica. "
    }
  ];

  return (
    <ChakraProvider>
      <Container maxW="80rem" centerContent>
        <SimpleGrid columns={[1, 2, 1, 2]}>
          {dataList.map(function (data) {
            const { id, product, summary, longLine } = data;
            return (
              <Card
                key={id}
                product={product}
                summary={summary}
                longLine={longLine}
              />
            );
          })}
        </SimpleGrid>
      </Container>
    </ChakraProvider>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
